export default {
	data() {
		return {
			tab: [
				{ name: '推荐', title: '猜你喜欢' },
				{ name: '好景点', title: '周末放空' },
				{ name: '人文', title: '文化之旅' },
				{ name: '海岛', title: '阳光与沙滩' },
				{ name: '自由行', title: '超值精选' },
				{ name: '网红', title: '最火打卡地' }
			],
			num:0,
		};
	},
	methods:{
		active(index){
			this.num=index
			this.$emit('tab',index)
		}
	}
};
